import React, { useEffect, useRef, useState } from 'react';
import mapboxgl from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';
import { Incident, LiveLocation } from '../types';
import { Zone } from '../hooks/useZones';

mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN || 'pk.eyJ1IjoibmlraGlsMjEwMjA2IiwiYSI6ImNta2U0NG0zdTAzMzUzZXMwZjZwbXFzZ3kifQ.fgjpDhGp_9bUapwaLEvtsg';

interface MapViewProps {
  incidents?: Incident[];
  locations?: LiveLocation[];
  zones?: Zone[];
  selectedIncidentId?: string | null;
  onSelectIncident?: (id: string) => void;
  loading?: boolean;
  supabaseEnabled?: boolean;
  showHeatmap?: boolean;
  mapStyle?: string;
}

// Helper: Check if point is inside a circle (for zone detection)
function isPointInZone(lat: number, lng: number, zone: Zone): boolean {
  const R = 6371; // Earth radius in km
  const dLat = (zone.lat - lat) * Math.PI / 180;
  const dLon = (zone.lng - lng) * Math.PI / 180;
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(lat * Math.PI / 180) * Math.cos(zone.lat * Math.PI / 180) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c * 1000; // Convert to meters

  return distance <= (zone.radius || 200);
}

export const MapView: React.FC<MapViewProps> = ({
  incidents = [],
  locations = [],
  zones = [],
  selectedIncidentId,
  onSelectIncident,
  loading = false,
  supabaseEnabled = false,
  showHeatmap = false,
  mapStyle = 'mapbox://styles/mapbox/dark-v11'
}) => {
  const mapContainer = useRef<HTMLDivElement>(null);
  const map = useRef<mapboxgl.Map | null>(null);
  const markersRef = useRef<mapboxgl.Marker[]>([]);
  const [mapLoaded, setMapLoaded] = useState(false);

  // Initialize Map
  useEffect(() => {
    if (map.current || !mapContainer.current) return;

    map.current = new mapboxgl.Map({
      container: mapContainer.current,
      style: mapStyle,
      center: [79.1559, 12.9692], // VIT Vellore
      zoom: 13.5,
      pitch: 45,
      attributionControl: false
    });

    map.current.on('load', () => {
      setMapLoaded(true);

      // Add zone sources
      map.current?.addSource('zones', {
        type: 'geojson',
        data: { type: 'FeatureCollection', features: [] }
      });

      // RED ZONES - Fill
      map.current?.addLayer({
        id: 'zones-red-fill',
        type: 'fill',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'HIGH'],
        paint: {
          'fill-color': '#FF4D4D',
          'fill-opacity': 0.25
        }
      });

      // RED ZONES - Border
      map.current?.addLayer({
        id: 'zones-red-border',
        type: 'line',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'HIGH'],
        paint: {
          'line-color': '#FF4D4D',
          'line-width': 2,
          'line-opacity': 0.8
        }
      });

      // YELLOW ZONES - Fill
      map.current?.addLayer({
        id: 'zones-yellow-fill',
        type: 'fill',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'MODERATE'],
        paint: {
          'fill-color': '#FFD700',
          'fill-opacity': 0.15
        }
      });

      // YELLOW ZONES - Border
      map.current?.addLayer({
        id: 'zones-yellow-border',
        type: 'line',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'MODERATE'],
        paint: {
          'line-color': '#FFD700',
          'line-width': 2,
          'line-opacity': 0.6
        }
      });
    });

    return () => {
      map.current?.remove();
      map.current = null;
    };
  }, [mapStyle]);

  // Update Zones
  useEffect(() => {
    if (!map.current || !mapLoaded) return;

    const zoneFeatures = zones.map(zone => {
      // Create circular polygon for each zone
      const center = [zone.lng, zone.lat];
      const radius = (zone.radius || 200) / 1000; // Convert to km
      const points = 64;
      const coords = [];

      for (let i = 0; i < points; i++) {
        const angle = (i / points) * 2 * Math.PI;
        const dx = radius * Math.cos(angle) / 111; // Rough conversion
        const dy = radius * Math.sin(angle) / (111 * Math.cos(zone.lat * Math.PI / 180));
        coords.push([center[0] + dx, center[1] + dy]);
      }
      coords.push(coords[0]); // Close the polygon

      return {
        type: 'Feature',
        geometry: {
          type: 'Polygon',
          coordinates: [coords]
        },
        properties: {
          id: zone.id,
          severity: zone.severity,
          location: zone.location
        }
      };
    });

    (map.current.getSource('zones') as mapboxgl.GeoJSONSource)?.setData({
      type: 'FeatureCollection',
      features: zoneFeatures as any
    });
  }, [zones, mapLoaded]);

  // Update User Markers (Beacons)
  useEffect(() => {
    if (!map.current) return;

    // Clear existing markers
    markersRef.current.forEach(m => m.remove());
    markersRef.current = [];

    locations.forEach(location => {
      // Check if user is in a danger zone
      let inRedZone = false;
      let inYellowZone = false;

      zones.forEach(zone => {
        if (isPointInZone(location.latitude, location.longitude, zone)) {
          if (zone.severity === 'HIGH') inRedZone = true;
          if (zone.severity === 'MODERATE') inYellowZone = true;
        }
      });

      // Create beacon element
      const el = document.createElement('div');
      el.className = 'user-beacon';
      el.style.position = 'relative';
      el.style.width = '32px';
      el.style.height = '32px';

      // Beacon dot
      const dot = document.createElement('div');
      dot.style.position = 'absolute';
      dot.style.top = '50%';
      dot.style.left = '50%';
      dot.style.transform = 'translate(-50%, -50%)';
      dot.style.width = inRedZone ? '20px' : '16px';
      dot.style.height = inRedZone ? '20px' : '16px';
      dot.style.borderRadius = '50%';
      dot.style.border = '3px solid white';
      dot.style.boxShadow = inRedZone
        ? '0 0 20px rgba(255, 77, 77, 0.8)'
        : inYellowZone
          ? '0 0 15px rgba(255, 215, 0, 0.6)'
          : '0 0 10px rgba(59, 130, 246, 0.5)';

      // Color based on zone
      if (inRedZone) {
        dot.style.backgroundColor = '#FF4D4D';
        dot.className = 'beacon-blink-red';
      } else if (inYellowZone) {
        dot.style.backgroundColor = '#FFD700';
        dot.className = 'beacon-blink-yellow';
      } else {
        dot.style.backgroundColor = '#3B82F6';
      }

      el.appendChild(dot);

      // Add directional arrow if heading is available
      if (location.heading !== null && location.heading !== undefined) {
        const arrow = document.createElement('div');
        arrow.style.position = 'absolute';
        arrow.style.top = '0';
        arrow.style.left = '50%';
        arrow.style.width = '0';
        arrow.style.height = '0';
        arrow.style.borderLeft = '6px solid transparent';
        arrow.style.borderRight = '6px solid transparent';
        arrow.style.borderBottom = '12px solid white';
        arrow.style.transform = `translateX(-50%) rotate(${location.heading}deg)`;
        arrow.style.transformOrigin = 'center 24px';
        arrow.style.filter = 'drop-shadow(0 0 3px rgba(0,0,0,0.5))';
        el.appendChild(arrow);
      }

      const marker = new mapboxgl.Marker({ element: el })
        .setLngLat([location.longitude, location.latitude])
        .setPopup(new mapboxgl.Popup({ offset: 25, className: 'beacon-popup' }).setHTML(`
          <div style="color: #fff; background: #18181b; padding: 8px; border-radius: 8px;">
            <div style="font-weight: bold; margin-bottom: 4px;">User: ${location.user_id}</div>
            <div style="font-size: 11px; color: #a1a1aa;">
              ${inRedZone ? 'ðŸ”´ IN RED ZONE' : inYellowZone ? 'ðŸŸ¡ IN YELLOW ZONE' : 'ðŸŸ¢ SAFE'}
            </div>
            ${location.speed ? `<div style="font-size: 11px; color: #a1a1aa;">Speed: ${location.speed.toFixed(1)} m/s</div>` : ''}
          </div>
        `))
        .addTo(map.current!);

      markersRef.current.push(marker);
    });
  }, [locations, zones]);

  // Render Incidents
  useEffect(() => {
    if (!map.current) return;

    incidents.forEach(incident => {
      const el = document.createElement('div');
      el.className = 'incident-marker';
      el.style.width = '24px';
      el.style.height = '24px';
      el.style.borderRadius = '50%';
      el.style.backgroundColor = incident.severity === 'high' ? '#EF4444' : '#F59E0B';
      el.style.border = '3px solid white';
      el.style.boxShadow = '0 0 15px rgba(239, 68, 68, 0.6)';
      el.style.cursor = 'pointer';
      el.style.animation = 'pulse 2s infinite';

      el.addEventListener('click', () => {
        if (onSelectIncident) onSelectIncident(incident.id);
      });

      const marker = new mapboxgl.Marker(el)
        .setLngLat([incident.longitude, incident.latitude])
        .setPopup(new mapboxgl.Popup({ offset: 25 }).setHTML(`
          <div style="color: #000; font-weight: bold;">${incident.display_name || 'Unknown'}</div>
          <div style="color: #555; font-size: 12px;">${incident.notes || 'No details'}</div>
        `))
        .addTo(map.current!);

      markersRef.current.push(marker);
    });
  }, [incidents, onSelectIncident]);

  return (
    <>
      <div ref={mapContainer} style={{ width: '100%', height: '100%' }} />
      <style jsx>{`
        @keyframes beacon-blink-red {
          0%, 100% { opacity: 1; transform: translate(-50%, -50%) scale(1); }
          50% { opacity: 0.6; transform: translate(-50%, -50%) scale(1.1); }
        }
        @keyframes beacon-blink-yellow {
          0%, 100% { opacity: 1; transform: translate(-50%, -50%) scale(1); }
          50% { opacity: 0.7; transform: translate(-50%, -50%) scale(1.05); }
        }
        .beacon-blink-red {
          animation: beacon-blink-red 1s infinite;
        }
        .beacon-blink-yellow {
          animation: beacon-blink-yellow 1.5s infinite;
        }
      `}</style>
    </>
  );
};

export default MapView;
